@keyframes levitation {
  from {
    top: 40%;
    opacity: 1;
  }
  to {
    top: 45%;
    opacity: 0.6;
  }
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  letter-spacing: 6px;
  animation-name: levitation;
  animation-duration: .6s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
