.chat {
  .top {
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    width: 100%;
    height: 70px;
    background-color: rgb(35, 35, 35);

    .back {
      padding: 15px 5px 15px 15px;
      user-select: none;
    }

    .avatar {
      width: 50px;
      height: 50px;
      margin: 0 calc((70px - 50px) / 2);
      background-size: cover;
      background-position: center;
      border-radius: 200px;
    }

    .name {
      font-size: 20px;
      text-decoration: none;
    }
  }

  .no-messages {
    position: absolute;
    top: calc((100vh - 70px - 185px) / 2 + 70px);
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    text-align: center;
    font-size: 26px;
    line-height: 35px;
    letter-spacing: 1px;

    span {
      margin-top: 10px;
      font-size: 50px;
    }
  }

  .messages {
    display: flex;
    flex-flow: column;
    width: 100%;
    margin-top: 70px;
    padding: 0 15px;
    padding-top: 15px;
    padding-bottom: 15px - 10px;

    .message {
      max-width: 85%;
      margin-bottom: 10px;
      padding: 10px;
      background-color: rgb(35, 35, 35);
      border-radius: 20px;
    }
  }

  .send {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 0 5px 5px 5px;
    background-color: #181818;

    textarea {
      width: 100%;
      max-height: 200px;
      padding: 6px;
      border: none;
      border-radius: 8px;
      border-bottom: 1px solid #000000;
      background-color: rgb(35, 35, 35);
      outline: none;
      font-size: 20px;
      resize: none;
      z-index: 1;
    }
  }
}
