.chats {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: calc((100vw - 85%) / 2) 0 calc((100vw - 85%) / 2 + 54px - 20px) 0;

  .no-chats {
    position: fixed;
    top: calc(50% - 54px / 2);
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-flow: column;
    width: 85%;
    height: calc(85vh - 54px);

    justify-content: center;
    padding: 30px;
    text-align: center;

    p {
      font-size: 26px;
      line-height: 35px;
      letter-spacing: 1px;
    }

    span {
      margin-top: 10px;
      font-size: 50px;
    }
  }

  .chat-preview {
    display: flex;
    align-items: center;
    width: 85%;
    height: 70px;
    margin-bottom: 20px;
    padding: 0 calc((70px - 50px) / 2);
    background-color: rgb(35, 35, 35);
    border-radius: 20px;
    text-decoration: none;

    .avatar {
      width: 50px;
      height: 50px;
      margin-right: calc((70px - 50px) / 2);
      background-size: cover;
      background-position: center;
      border-radius: 200px;
    }

    .info {
      display: flex;
      flex-flow: column;
      justify-content: space-evenly;
      height: 50px;

      .name {
        font-size: 20px;
      }

      .preview-text {
        width: calc(85vw - 80px);
        font-size: 14px;
        color: rgba(255, 255, 255, 0.6);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
}
